
    import {apiService, apiUrl} from '@/api'
	import LoadMore from 'vux/src/components/load-more/index.vue'

	export default {
		components: {
			LoadMore
		},
		data() {
			return {
				dataList: [],
				paramList: {//文章列表
					pageNum: 0,
					pageSize: 2,
					itemid: ""
				},
				isLoad:false,
				isNext: true,
				code: '',
				origin: '',
				userId: 0
			}
		},
		created() {
			var vm = this;
			vm.paramList.itemid = this.$route.query.itemid;
			vm.code = this.$route.query.code;
			vm.userId = this.$route.query.userId;
			vm.origin = window.location.origin
			vm.getArticleList();
		},
		methods: {
			getArticleList() {//获取文章列表
				var vm = this;
				var url = apiUrl.baseUrl + "article/getArticle/"+ vm.paramList.pageNum+ "/" + vm.paramList.pageSize+ '/'+ vm.paramList.itemid;
				apiService.requestGet(url).then((res)=> {
					if(res.data.length != 0){
						vm.dataList.push(res.data);
						// for(var item of res.data){
						// 	vm.dataList.push(item);
						// }
						vm.isNext = true;
					}else {
						vm.isNext =false;
					}
					vm.isLoad = false;
					 
				},(res)=> {
					console.log("数据错误");
					vm.isNext =false;
					vm.isLoad = false;
				})
			},
			getArticleListMore(){
				var vm =this;
				vm.paramList.pageNum = vm.paramList.pageNum +1;
				vm.getArticleList();
				vm.isLoad= true;
			}

		},
		computed: {

		},
		watch: {

		},
		filters: {
			listTime (val){
			var week = ['星期日', '星期一','星期二' ,'星期三' ,'星期四','星期五', '星期六'];
			function getTime(tm){
				var date = new Date(tm);
				return {
					y: date.getFullYear(),
					m: date.getMonth()+1,
					d: date.getDate(),
					h: date.getHours(),
					min: date.getMinutes(),
					w: date.getDay()
				}
			}
			function twoParse(val){
				var v = val.toString();
				return v.length == 2 ? v: '0'+ v;
			}

			function hpp (val){
				var tep = "";
				if(so.h - 12 < 0){
					tep = '上午 ' + so.h;
				}else if(so.h - 12 ==0) {
					tep = '中午 ' + so.h;
				}else {
					tep = '下午 ' + (so.h - 12);
				}


				return tep;
			}

			function wpp (val){
				return week[val];
			}
				var now = new Date();
				var sd = new Date(val);
				var no = getTime(now.getTime());
				var so = getTime(sd.getTime());
				var h2 = 48*60*60*1000;

				var d7 = 24*60*60*1000*6;
				if(now.getTime() - sd.getTime() < h2){
					if(no.d - so.d  == 0){
						return hpp(so.h) + ':' + twoParse(so.min);
					} else if (no.d - so.d == 1){
						return '昨天 '+ hpp(so.h) + ':' + twoParse(so.min);
					} else {
						return week[so.w]  + hpp(so.h) + ':' + twoParse(so.min);
					}
				}else if(now.getTime() - sd.getTime() < d7){
					return week[so.w]  + hpp(so.h) + ':' + twoParse(so.min);

				} else {
					 return  so.y + '年' + so.m+ '月' + so.d + '日  ' + hpp(so.h) + ':' + twoParse(so.min);
				}
			}
		}
	}



